body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

Header{
  background-color: black;
  color: white;
  padding: 0.5%;
  display: flex;
  
}
    Header h2{
    font-size: 200%;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    flex: 1 1;
  }

    header img{
      width: 5%;
      margin: 0.5%;
  }

.reloj{
  margin: 3%;
  text-align: center;
}

Section{
  height: 54.2vh;
  display: flex;

}
    Section Table{
      padding-left: 10%;
      flex: 1 1;
      display: flex;
      flex-flow: column nowrap;
    }
      Section Table thead {
        display: flex;
      }

        Section Table thead tr{
            flex: 1 1;
            display: flex;
            padding: 2%;
        }
          Section Table thead tr th{
            flex: 1 1;
          }

      Section Table tbody{
        display: flex;
        flex-flow: column nowrap;
      }
        Section Table tbody tr{
          flex: 1 1;
          display: flex;
          padding: 2%;
        }
          Section Table tbody tr td{
            flex: 1 1;
            text-align: center;
          }
              .delete button{
                  background-color: red;
                  color: white;
                  border: none;
                  padding: 7%;
                  border-radius: 10%;
              }


    Section form{
      padding-right: 10%;
      padding-left: 20%;
      display: flex;
      flex-flow: column nowrap;
      flex: 1 1;
    }
        section form input{
            padding: 2%;
        }

        section form input.boton{
          margin-top: 4%;
          background-color: green;
          border: none;
          border-radius: 3%;
          color: white;
        }

Footer{
  background-color: black;
  color: white;
  padding: 1%;
  display: flex;
  justify-content: center;
}